<template>
<div>
    <table class="table table-hover">
        <thead class="table-dark">
            <tr>
                <th width="10" class="rounded-start">#</th>
                <th width="35%">Tên công ty</th>
                <th>Mã số thuế</th>
                <th>Ngày HT/Xuất HĐ</th>
                <th>Loại HĐ</th>
                <th>Khu vực</th>
                <th>Tỷ lệ</th>
                <th>Doanh thu</th>
                <th class="rounded-end">Tiền PTTB</th>
            </tr>
        </thead>
        <tbody>
            <tr>
                <td></td>
                <td>
                    <input v-model="searchBy.company_name" type="text" placeholder="Tìm theo tên công ty" class="form-control" />
                </td>
                <td>
                    <input v-model="searchBy.company_taxcode" type="text" placeholder="Tìm theo mã số thuế" class="form-control" />
                </td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
            </tr>
            <tr v-for="(item, index) in revenuesfiltered" :key="item.contract_id">
                <td>{{ index + 1 }}</td>
                <td>
                    <router-link :to="{
                name: 'contract-detail',
                params: { id: item.contract_id },
              }" :title="item.company_name" class="fw-bold">
                        {{ item.company_name }}
                    </router-link>
                    <div>
                        <i class="text-danger">{{ item.contract_note }}</i>
                    </div>

                    <ul class="list-group" v-for="detail in item.details" :key="detail.detail_id">
                        <li class="list-group-item m-1">
                            <div><strong>Nội dung: </strong>{{ detail.detail_content }}</div>
                            <div>
                                <strong>Thành tiền:</strong>
                                {{ detail.detail_product_price | formatamout() }}
                            </div>
                        </li>
                    </ul>
                </td>
                <td>{{ item.company_taxcode }}</td>
                <td>{{ item.complete_date | moment("DD/MM/YYYY") }}</td>
                <td>
                    <template v-if="item.contract_type == 1">
                        <span class="badge bg-warning bg-opacity-10 text-black"><i class="fa-solid fa-circle-small"></i> Gia hạn</span>
                    </template>
                    <template v-else>
                        <span class="badge bg-success bg-opacity-10 text-success"><i class="fa-solid fa-circle-small"></i> Mới</span>
                    </template>
                </td>
                <td>
                    <template v-if="item.contract_area == 1">
                        <span class="badge bg-warning bg-opacity-10 text-black"><i class="fa-solid fa-circle-small"></i> Huyện</span>
                    </template>
                    <template v-else>
                        <span class="badge bg-success bg-opacity-10 text-success"><i class="fa-solid fa-circle-small"></i> Thành phố</span>
                    </template>
                </td>
                <td>{{ item.ratio_value }}</td>
                <td>{{ calculatorRevenues(item) | formatamout }}đ</td>
                <td>{{ calSalaryPTTB(item) | formatamout }}đ</td>
            </tr>
        </tbody>
    </table>
</div>
</template>

<script>
import {
    mapActions,
    mapGetters
} from "vuex";
export default {
    props: {
        revenues: Array,
    },
    data() {
        return {
            searchBy: {
                company_name: "",
                company_taxcode: "",
            },
        };
    },

    computed: {
        ...mapGetters("revenues", ["totalPTTB"]),
        revenuesfiltered() {
            let result = this.revenues.filter((item) => {
                return (
                    item.company_name
                    .toLowerCase()
                    .indexOf(this.searchBy.company_name.toLowerCase()) !== -1 
                    // ||
                    //  item.company_taxcode.toLowerCase().indexOf(this.searchBy.company_taxcode.toLowerCase()) !==-1
                );
            });

            return result;
        },
    },
    methods: {
        ...mapActions("revenues", ["calRevenues"]),
        calculatorRevenues(contract) {
            return this.calSalary(contract);
        },

        calSalary(contract) {
            let details = contract.details;

            let price = 0;
            let ratio = contract.ratio_value !== null ? contract.ratio_value : 0.1;
            price = details.reduce((total, detail) => {
                if (detail.product.product_split)
                    return (
                        total +
                        parseFloat(
                            (detail.detail_product_price / contract.contract_expired_month) *
                            detail.detail_quantity
                        )
                    );

                return total + parseFloat(detail.detail_product_price * detail.detail_quantity);
            }, 0);

            return price;
        },

        calSalaryPTTB(contract) {
            let details = contract.details;

            let price = 0;
            let ratio = contract.ratio_value !== null ? contract.ratio_value : 0.1;
            price = details.reduce((total, detail) => {
                return (
                    total +
                    parseFloat(detail.detail_product_price * detail.detail_quantity) *
                    parseFloat(ratio)
                );
            }, 0);

            return price;
        },
    },
};
</script>
